export const isPointerEvent = (event) =>
  self.PointerEvent && event instanceof PointerEvent;
export const noop = () => {};
export const getDistance = (a, b) =>
  !b
    ? 0
    : Math.sqrt(
        Math.pow(b.clientX - a.clientX, 2) + Math.pow(b.clientY - a.clientY, 2)
      );
export const getMidpoint = (a, b) => {
  if (!b) {
    return a;
  }
  return {
    clientX: (a.clientX + b.clientX) / 2,
    clientY: (a.clientY + b.clientY) / 2,
  };
};
export const getAbsoluteValue = (value, max) => {
  if (typeof value === "number") {
    return value;
  }
  if (value.trimRight().endsWith("%")) {
    return (max * parseFloat(value)) / 100;
  }
  return parseFloat(value);
};
export const classNames = (base, other) => (other ? `${base} ${other}` : base);
