import React, { useRef, useCallback } from "react";
import { GET_MEDIA_NAME } from "../../../../../Services/Utils/utils";
import {
    PinchZoom,
    applyCssProperties,
    getTransform3DValue,
    getTransform2DValue,
} from "react-fast-pinch-zoom";
import { baseUrl } from '../../../../../Assets/AssetsHelper';
const Media = React.lazy(() => import('../../../CommonComponents/Media/Media'));
const PopUpVideo = React.lazy(() => import('../../../CommonComponents/Media/Elements/PopUpVideo'));


const ModalMedia = (props) => {
    const ref = useRef(null);
    const onTransform = useCallback(({ x, y, scale }) => {
        // Use CSS Custom Properties (Variables)
        applyCssProperties(ref.current, { x, y, scale });

        // Or use literal transform value
        // ref.current.style.setProperty('transform',getTransform3DValue({x,y,scale}));
        // ref.current.style.setProperty('transform',getTransform2DValue({x,y,scale}));
    }, []);
    const onErrorImageUpdate = event => {
        event.target.src = baseUrl(`media/images/error/image.svg`)
    }
    const socialNetwork = props.networkData.filter((network) => network.id == props.modalAssetData.networkId)
    return <div className="assets_modal_img_outer">
        <div className="assets_modal_img_wrapper">
            {(props.modalAssetData.type == 2 || props.modalAssetData.type == 4) ? <PinchZoom onTransform={onTransform}>
                <img ref={ref} className="assets_modal_media__" src={props.modalAssetData.postFile} height={200} width={1200} alt={GET_MEDIA_NAME(props.modalAssetData.postFile)} onError={onErrorImageUpdate} />
            </PinchZoom> : (props.modalAssetData.type == 1) ? <Media asset={props.modalAssetData} animation={'fade'} socialNetwork={socialNetwork} key={`post_image${props.modalAssetData.id}`} /> : (props.modalAssetData.type == 3 || props.modalAssetData.type == 5) ? <PopUpVideo data={props.modalAssetData} /> : null

            }
        </div>
    </div>

};
export default ModalMedia;